body {
  margin: 0;
  line-height: normal;
}

.popup-overlay {
  flex-direction: column;
  display: flex;
  position: fixed;
  inset: 0;
}

.video {
  height: 560px;
  flex-shrink: 0;
  align-self: stretch;
  position: relative;
}

.matterhorn-popup {
  border-radius: var(--br-2xs);
  background-color: var(--studio-darkmode-allwhite-ffffff);
  width: 900px;
  padding: var(--padding-md);
  box-sizing: border-box;
  max-width: 90%;
  max-height: 90%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
  position: relative;
  overflow: auto;
}

@media screen and (max-width: 650px) {
  .video {
    height: 50.625vw !important;
  }

  .matterhorn-popup {
    padding: var(--padding-5xs);
    box-sizing: border-box;
  }
}

